body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  line-height: normal;
}

body {
  min-height: 100vh;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.App {
  text-align: center;
  border: 2px solid blue;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.2);

}

.MainTemp {
  padding: 20px;
  font-size: 1.5em;
  background-image: -webkit-linear-gradient(#6ebcfc, #65fede, #6ebcfc);
  background-image: linear-gradient(#6ebcfc, #65fede, #6ebcfc);
  border: 2px solid #fff;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

}

.SearchBox {
  padding: 20px;
  font-size: 1.5em;
  background-image: -webkit-linear-gradient(#65fede, #6ebcfc);
  background-image: linear-gradient(#65fede, #6ebcfc);
  border: 2px solid #fff;
  border-radius: 10px;
}


.Forecast {
  padding: 20px;
  font-size: 1.5em;
  background-image: -webkit-linear-gradient(#6ebcfc, #65fede);
  background-image: linear-gradient(#6ebcfc, #65fede);
  border: 2px solid #fff;
  border-radius: 10px;
}

